.product-slider .swiper-slide * {
  display: flex;
  align-items: center;
  justify-content: center;
}
@media (max-width: 768px) {
  .product-slider .swiper-slide img, .product-slider .swiper-slide picture {
    display: flex;
    object-fit: contain;
  }
}